					.slideshow__item {
						.gallery__img {
							max-height:calc(100vh - 8.4rem);
							height:calc(100vh - 8.4rem);
							height: calc(var(--vh, 1vh) * 100 - 8.4rem);
						}
					}
				}

				.slideshow__counter {
	
					.counter__wrapper {
						width:40%;
					}

					.img__caption {
						@include font-size ($fs-s-desktop);
					}
				}
			}
		}

		.project__overview {

				.project__layer__body {
				
					.overview__thumbnail__wrapper {
						width:50vw;

						.overview__thumbnail {
							
							.item__img__text {
								@include font-size($fs-s-desktop);
							}
						}

					}
				}
		}
	}

}

@media (min-width: $breakpoint-desktop-to-xl) {

	#project__header {

		.projectnav {
			padding-left:16.6666%;
			width:33.3333%; 
		}

		.projecttitle__wrapper {
			width:33.3333%;

			.projecttitle__name {
				flex:3;
			}
		}
	}

	#project {

		.project__images {
			.images__slideshow {
				.slideshow__counter {
					.counter__wrapper {
						width:33.333%;
					}
				}
			}
		}

		.project__information {
			
			.project__layer__body {
			
				.information__meta {
					width:33.333%;
					// width:24.9999%;
					padding-left:8.33333%;
				}

				.information__description {
					width:39.3%;
					padding-left:8.33333%;
				}
			}
		}

		.project__overview {

			.project__layer__body {
			
				.overview__thumbnail__wrapper {

					.overview__thumbnail {
						width:8.3vw;
					}
				}
			}
		}
	}	

}