@media (min-width: $breakpoint-tablet-hoch-to-tablet-quer) and (max-width: $breakpoint-tablet-quer-to-desktop) {

	#general__header {

		.page__title {
			padding-left:75%;
		}
	} 
}

@media (min-width: $breakpoint-desktop-to-xl) {

	#general__header {

		.page__title {
			padding-left:83.3333%;
		}
	}

	#general {
		width:99.3%;

		.general__wrapper {
			columns:6;
		}
	}
}