  // font-family: "Gotham-Book";
  // font-family: "NeuzeitSLTStd-Book";
  // font-family: "EuclidCircularATrial-Regular";
  
  @include font-size($fs-m-mobile);
  line-height:1;

  // -ms-hyphens: auto;
  // -moz-hyphens: auto;
  // -webkit-hyphens: auto;
  // -khtml-hyphens: auto;
  // hyphens: auto;

  &.page--project {
    overflow:hidden;
    
    .overflow__wrapper {
      position: relative;
      overflow:hidden;
    }
  }
}

::-moz-selection {
  color: black; background: $color-projectinformation;
}

::selection {
  color: black; background: $color-projectinformation;
}

.initialload {
  opacity:0 !important;
}

.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
}

.transition-fade {
  transition: 0.4s;
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
}

.desktop {
  display:none
}

.mobile {
  display:inline-block
}

.overflowhidden {
  overflow-y:hidden !important;
  
  #swup, .overflow__wrapper {
    overflow-y:hidden !important;
  }
}

sup {
  vertical-align: super;
 font-size:60%;
}

em {
  font-style:italic;
}

#grid {
  position:fixed;
  width:100vw;
  height:100vh;
  pointer-events:none;
  mix-blend-mode: multiply;
  opacity:0.5;
  z-index:100000;

  img {
    width: 100%;
  }
}

a {
  color:black;
}

body.hasHover {

  a {
    &:hover {
      font-style:italic !important
    }
  }

  #header {
    .header__menutoggle {
      .header__menutoggle__item {
        &:hover {
          font-style: italic;
        }
      }
    }
	}

  #project__header {
    .projecttoggle {
      .projecttoggle__item {
        &:hover {
          font-style: italic;
        }	
        &.inactive {
          &:hover {
            font-style: italic;
          }
        }
      }
    }
  }

  .tooltip {
		position: absolute;
		display: block;
		z-index: 2000;
		pointer-events: none;
  }

}

.language__item, .language {

  // display:flex;
  flex-direction: column;
  // padding-top:15px;
