}

.page__header {
  display:flex;
  position:fixed;
  top:0;
  width:100%;
	// @include padding-top(3.3rem);
  padding-top:33px;
	// @include padding-top(1.5rem);
  z-index:80;
  background:white;
  
  
  &.hidden {
    opacity:0
  }
  
  &.visible {
    opacity:1
  }
  
  .projectlink__url {
    display: flex;
    flex-direction: row;
    margin-left:0;
    width:100%;
    // padding-top:$site-padding;
    
    .projecttitle__number {
      flex:1;
      width:33.3333%;
      // @include padding-top(0.8rem);
      // @include padding-left($site-padding);
      padding-left:$lateral-page-gaps-text;
    }
    
    .projecttitle__name__wrapper {
      flex:2;
      width:33.3333%; 
      // @include padding-top(0.5rem);
      @include padding-right(0.5rem);
    }
    
    .projectlink {
      @include padding-right(0.5rem);
      width:10%;
      
      &.hidden {
        opacity:0;
        pointer-events: none;
      }
      
      &.visible {
        opacity:1;
        pointer-events: all;
      }
    }
  }
}


@media (min-width: $breakpoint-mobile-to-tablet-hoch) {

  body {
    @include font-size($fs-m-tablet);
  }

  .page__header {
    
    .projectlink__url {
      
      .projecttitle__number {
        flex:1;
      }
      
      .projecttitle__name__wrapper {
        flex:4;
      }
    }
  }

}

@media (min-width: $breakpoint-tablet-hoch-to-tablet-quer) {

  .desktop {
    display:inline-block
  }
  
  .mobile {
    display:none
  }

  .language__item, .language {
    padding-top:30px;
    // width:11vw
    // width:10%

    // position: fixed;
    // margin-left:90vw;

    // &.desktop {
    //   display:flex;
    //   pointer-events: all;
    // }

    // &.mobile {
    //   display:none;
    //   pointer-events: none;
    // }
  }

  .page__header {
    // @include padding-top(0.1rem);
    padding-top:$site-padding;
    // padding-top:0;
    height:$page-header-height-desktop;
    
    .projectlink__url {
      flex-direction: row;
      margin-left:37.5%;
      width:37.5%;
      // padding-top:$site-padding;
      
      .projecttitle__number {
        flex:1;
        width:10%;
        padding-left: 0;
        padding-top: 0;
      }
      
      .projecttitle__name__wrapper {
        flex:2;
        width:20%;
        padding-top: 0;
      }
      
      .projectlink {
        @include padding-right(0.5rem);
        width:10%;
        
        &.hidden {
          opacity:0;
          pointer-events: none;
        }
        
        &.visible {
          opacity:1;
          pointer-events: all;
        }
      }
    }
  }

}


@media (min-width: $breakpoint-tablet-quer-to-desktop) {

  body {
    @include font-size($fs-m-desktop);
  }

  .language__item, .language {
    // width:9.75vw;
  }

  .page__header {
    
    .projectlink__url {
      padding-top:$site-padding;
      flex-direction: row;
      margin-left:50%;
      width:30%;
    }
  }
}


@media (min-width: $breakpoint-desktop-to-xl) {

  .language__item, .language {
    // width:8.5vw;
  }

  .page__header {
    
    .projectlink__url {
      margin-left:50%;
      width:33.3333%;
      
      .projecttitle__number {
        flex:1;
        width:8.5vw;
        padding-left: 0;
      }
      
      .projecttitle__name__wrapper {
        flex:3;
        // width:20%;
      }
    }
  }
}